	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Twenty Percent","date":"21 May 2003","slug":"/2003-05-21-twenty-percent/","tags":"Y2003"},"html":"<p>At work, it looks like things might be coming together for <a href=\"http://www.papascott.de/2003/05/08/2214.php\">reduced hours</a> starting June 1. The proposal for our department is an across-the-board 20% reduction. That means we can work 4 day weeks, or work 2 half-days and 3 full-days or whatever. The state (un)employment office will cover 60% of lost wages for 6 months. That's a solution that's fair and that I can live with, although I don't really have any confidence that things will be better in 6 months, either with my present employer or the job market in general.</p>\n<p>Meanwhile, my new <a href=\"http://www.nokia.com/nokia/0,,2273,00.html\">Nokia 3650</a> cell phone arrived last night, and Mama had what she called 'car pornography' (brochures for the <a href=\"http://www.bmw.de/de/produkte/automobiles/index2.html?model=5series\">new BMW 5 series</a>), so we each had our entertainment last night after Christopher was asleep.</p>"},"next":{"fields":{"title":"The Bird Goes to Work","date":"21 May 2003","slug":"/2003-05-21-the-bird-goes-to-work/"}},"previous":{"fields":{"title":"Does this thing work?","date":"22 May 2003","slug":"/2003-05-22-does-this-thing-work/"}}},{"node":{"fields":{"title":"The Bird Goes to Work","date":"21 May 2003","slug":"/2003-05-21-the-bird-goes-to-work/","tags":"Y2003"},"html":"<p><a title=\"Knock 'em dead, Shelley!\" href=\"http://weblog.burningbird.net/fires/001226.htm\">The Bird goes to work</a></p>"},"next":{"fields":{"title":"Faux Pax Americana","date":"21 May 2003","slug":"/2003-05-21-faux-pax-americana/"}},"previous":{"fields":{"title":"Twenty Percent","date":"21 May 2003","slug":"/2003-05-21-twenty-percent/"}}},{"node":{"fields":{"title":"Faux Pax Americana","date":"21 May 2003","slug":"/2003-05-21-faux-pax-americana/","tags":"Y2003"},"html":"<p><a title=\"Plenty of troops to win the war, not nearly enough to win the peace\" href=\"http://www.washingtonmonthly.com/features/2003/0306.carter.html\">Faux Pax Americana</a></p>"},"next":{"fields":{"title":"Et tu, Bavaria?","date":"21 May 2003","slug":"/2003-05-21-et-tu-bavaria/"}},"previous":{"fields":{"title":"The Bird Goes to Work","date":"21 May 2003","slug":"/2003-05-21-the-bird-goes-to-work/"}}}],"pathPrefix":"","first":false,"last":false,"index":729,"pageCount":1101}}

